import '../../../../src/css/app.css';
import { useRef, useState } from "react";
import './header.scss';
import img from "./img-my.jpg";
function Header() {
   const [todos, setTodos] = useState([]);
   const ref = useRef();
   const hendlerTodo = (e) => {
      if (e.code === 'Enter') {
         const todosArr = {
            id: new Date().getTime(),
            title: e.target.value,
            isComplite: false,
         }
         
         setTodos([todosArr, ...todos]);
         e.target.value = null;
      }
   }
   
   const [count, setCount] = useState(0);
   const countLength = (evn) => {
      const elem = evn.target;
      setCount(elem.value.length);
   }
   
   return(
      <>
      <header className="header">
      <button
      onClick={(e) => {
         if (document.body.className === ("light")) {
            document.body.className = "dark"
            
         } else if(document.body.className.includes("dark")){
            document.body.className = "light"
         }
         const iconSun = document.querySelector(".iconSun")
         const iconMoon = document.querySelector(".iconMoon")
         if (iconSun.className.includes("click__icon")) {
            iconSun.classList.remove("click__icon")
            iconMoon.classList.add("click__icon")

         } else if(iconMoon.className.includes("click__icon")){
            iconMoon.classList.remove("click__icon")
            iconSun.classList.add("click__icon")
         }
      }
      }
      className="icons">
         <div className="iconSun click__icon">
            <i className='bx bxs-sun' style={{"color": "rgba(255, 164, 0, 1)",}} ></i>
         </div>
         <div className="iconMoon" >
            <i className='bx bxs-moon' ></i>
         </div>
      </button>
         <select className="selectComplite" name="selec" id="select"
         onChange={(e) => {
            console.log(e.target.value);
         }}
         >
            <option className="optionSelect" value="all">All</option>
            <option className="optionSelect" value="complite">Complite</option>
            <option className="optionSelect" value="disComplite">DisComplite</option>
         </select>
      <div className="header__top">
      <div className="header__write">
      <h1>Todo List</h1>
      <input
      placeholder="type your text.."
      className="input"
      onKeyUp={hendlerTodo}
      onChange={countLength}
      type="text" />
      <p className="count">count: {count}</p>
      <p className="header__text">Create: <span className="color">Islom Abduvoxidov <span className="stik">ã‚·</span></span></p>
      </div>
      <img className="img" src={img} alt="img" />
      </div>
      
      <ul className="list">
      {
         todos.map((todo, i) => {
            return(
               <li 
               className={todo.isComplite === true ? "list__item complite" : "list__item"}
               key={todo.id}>
               <input
               datatype={todo.id}
               onChange={() => {
                  setTodos(
                     todos.map((item) => {
                        if(item.id == todo.id){
                           return {
                              ...item, isComplite: !item.isComplite,
                           }
                        }
                        return item;
                     })
                     )
                  }}
                  className="checkbox" 
                  type="checkbox" />
                  <p  datatype={todo.id} ref={ref} className={todo.isComplite === true ? "list__text line" : "list__text"}><span className="index">{i + 1}: </span> {todo.title}</p>
                  <button
                  className="list__btn"
                  onClick={() => {
                     setTodos(todos.filter((e) => e.id !== todo.id ));
                  }
               }>delete</button>
               </li>
               )
            })
         }
         </ul>
         </header>
         </>
         )
      }
      
      export default Header